// eslint-disable-next-line import/no-extraneous-dependencies
import * as yup from 'yup';

export const loginSchema = yup
  .object({
    email: yup
      .string()
      .required(' "email"   .')
      .matches(
        /^\S+@\S+\.[a-zA-Z]{2,}$/,
        ',   email.'
      ),
    password: yup
      .string()
      .required(' ""   .')
      .matches(
        /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{8,15}$/,
        '    8  15 ,   ,    .'
      ),
  })
  .required();

export const restorePasswordSchema = yup
  .object({
    email: yup
      .string()
      .required(' "email"   .')
      .matches(
        /^\S+@\S+\.[a-zA-Z]{2,}$/,
        ',   email.'
      ),
  })
  .required();

export const resetPasswordSchema = yup
  .object({
    password: yup
      .string()
      .required(' ""   .')
      .matches(
        /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{8,15}$/,
        '    8  15 ,   ,    .'
      ),
    confirmPassword: yup
      .string()
      .required(' " "   .')
      .oneOf([yup.ref('password'), null], '  .'),
  })
  .required();
